"use client";
import { useState } from "react";
import { motion, AnimatePresence } from "framer-motion";
import dynamic from "next/dynamic";
const PdfViewer = dynamic(() => import("./PdfViewer"), { ssr: false });

// Subject list
const subjects = [
  { name: "Mathematic's" },
  { name: "Physics" },
  { name: "Chemistry" },
];

// Sidebar with modern glassmorphism and icon style
function Sidebar({
  subjects,
  expandedSubjects,
  toggleSubject,
  expandedLesson,
  toggleLesson,
}: {
  subjects: { name: string }[];
  expandedSubjects: { [key: string]: boolean };
  toggleSubject: (subject: string) => void;
  expandedLesson: string | null;
  toggleLesson: (lesson: string) => void;
}) {
  return (
    <aside className="w-full max-w-[320px] min-w-[220px] bg-white/70 backdrop-blur-md shadow-xl border-r border-gray-200 px-0 py-0 flex flex-col h-full">
      {/* Sidebar Title */}
      <div className="flex items-center gap-2 px-6 py-6 border-b border-gray-100">
        <span className="inline-block bg-gradient-to-tr from-blue-500 to-cyan-400 rounded-full p-2">
          <svg width={28} height={28} fill="none" viewBox="0 0 24 24">
            <circle cx="12" cy="12" r="10" fill="#fff" />
            <path d="M8 12h8M12 8v8" stroke="#3b82f6" strokeWidth="2" strokeLinecap="round" />
          </svg>
        </span>
        <span className="font-extrabold text-2xl text-gray-800 tracking-tight">Recall Notes</span>
      </div>
      {/* Subject Dropdowns */}
      <div className="flex-1 overflow-y-auto px-2 py-4">
        {subjects.map((subject) => (
          <div key={subject.name} className="mb-2">
            <button
              className={`w-full flex items-center justify-between px-4 py-3 rounded-xl transition-all duration-200
                ${
                  expandedSubjects[subject.name]
                    ? "bg-gradient-to-r from-blue-100 to-cyan-100 text-blue-700 font-semibold shadow"
                    : "bg-white/60 hover:bg-blue-50 text-gray-700"
                }`}
              onClick={() => toggleSubject(subject.name)}
            >
              <span className="flex items-center gap-2">
                <span className="inline-block w-2 h-2 rounded-full mr-2"
                  style={{
                    background:
                      subject.name === "Mathematic's"
                        ? "#f59e42"
                        : subject.name === "Physics"
                        ? "#60a5fa"
                        : "#f472b6",
                  }}
                ></span>
                {subject.name}
              </span>
              <span className="ml-2">
                <svg
                  className={`w-4 h-4 transition-transform duration-200 ${
                    expandedSubjects[subject.name] ? "rotate-180" : ""
                  }`}
                  fill="none"
                  stroke="currentColor"
                  strokeWidth={2}
                  viewBox="0 0 24 24"
                >
                  <path d="M19 9l-7 7-7-7" />
                </svg>
              </span>
            </button>
            <AnimatePresence initial={false}>
              {expandedSubjects[subject.name] && (
                <motion.div
                  className="ml-4 mt-1 pl-2 border-l-2 border-blue-100 flex flex-col gap-1"
                  initial={{ height: 0, opacity: 0 }}
                  animate={{ height: "auto", opacity: 1 }}
                  exit={{ height: 0, opacity: 0 }}
                  transition={{ duration: 0.22 }}
                >
                  {[...Array(10)].map((_, i) => {
                    const lesson = `Lesson ${i + 1}`;
                    return (
                      <button
                        key={lesson}
                        className={`w-full text-left px-3 py-2 rounded-lg transition-all duration-150 flex items-center justify-between
                          ${
                            expandedLesson === lesson
                              ? "bg-blue-100 text-blue-700 font-semibold"
                              : "bg-transparent text-gray-600 hover:bg-blue-50 hover:text-blue-700"
                          }`}
                        onClick={() => toggleLesson(lesson)}
                      >
                        <span>{lesson}</span>
                        <span>
                          <svg
                            className={`w-3 h-3 transition-transform duration-200 ${
                              expandedLesson === lesson ? "rotate-180" : ""
                            }`}
                            fill="none"
                            stroke="currentColor"
                            strokeWidth={2}
                            viewBox="0 0 24 24"
                          >
                            <path d="M19 9l-7 7-7-7" />
                          </svg>
                        </span>
                      </button>
                    );
                  })}
                </motion.div>
              )}
            </AnimatePresence>
          </div>
        ))}
      </div>
    </aside>
  );
}

// Main Recall Page
export default function RecallPage() {
  const [expandedSubjects, setExpandedSubjects] = useState<{ [key: string]: boolean }>({});
  const [expandedLesson, setExpandedLesson] = useState<string | null>(null);
  const [activeSubject, setActiveSubject] = useState<string | null>(null);

  // Toggle subject dropdown
  const toggleSubject = (subject: string) => {
    setExpandedSubjects((prev) => {
      const newState = Object.keys(prev).reduce((acc, key) => ({ ...acc, [key]: false }), {});
      return { ...newState, [subject]: !prev[subject] };
    });
    setActiveSubject(subject);
    setExpandedLesson(null);
  };

  // Toggle lesson dropdown
  const toggleLesson = (lesson: string) => {
    setExpandedLesson((prev) => (prev === lesson ? null : lesson));
  };

  // Responsive layout
  return (
    <div className="flex flex-col md:flex-row h-screen w-full font-sans bg-gradient-to-br from-blue-50 via-white to-cyan-50">
      {/* Sidebar */}
      <Sidebar
        subjects={subjects}
        expandedSubjects={expandedSubjects}
        toggleSubject={toggleSubject}
        expandedLesson={expandedLesson}
        toggleLesson={toggleLesson}
      />
      {/* Content: PDF Viewer in vertical layout */}
      <div className="flex-1 flex flex-col items-center justify-start p-4 md:p-10 overflow-y-auto">
        {activeSubject && expandedLesson ? (
          <div className="w-full max-w-2xl bg-white/90 rounded-3xl shadow-2xl p-8 md:p-12 border border-blue-100 animate-fadeIn flex flex-col items-center">
            <h3 className="text-3xl font-extrabold mb-4 text-blue-700 tracking-tight text-center">
              {activeSubject} <span className="text-gray-400">/</span> {expandedLesson}
            </h3>
            <p className="mb-8 text-gray-700 text-lg text-center">
              Select a lesson to view PDF
            </p>
          </div>
        ) : (
          <div className="text-gray-400 text-xl font-semibold flex flex-col items-center mt-20">
            <svg
              className="w-12 h-12 mb-2 text-blue-200"
              fill="none"
              stroke="currentColor"
              strokeWidth={1.5}
              viewBox="0 0 24 24"
            >
              <circle cx="12" cy="12" r="10" />
              <path d="M8 12h8M12 8v8" stroke="#60a5fa" strokeWidth="2" strokeLinecap="round" />
            </svg>
            Select a lesson to view PDF
          </div>
        )}
      </div>
    </div>
  );
}